import { Component, OnInit } from '@angular/core';

import {
  Card
} from 'dice-guardians-module'

import { DiceGuardiansModuleService } from '../../services/dice-guardians-module.service'

@Component({
  selector: 'app-battlefield',
  templateUrl: './battlefield.page.html',
  styleUrls: ['./battlefield.page.scss'],
})
export class BattlefieldPage implements OnInit {

  leftToggleName:string = 'arrow-back-outline'
  leftDrawerClass:string='shown'

  bottomToggleName:string = 'arrow-down-outline'
  bottomDrawerClass:string='shown'


  tutorialPointerStyle:string = ''
  tutorialParentStyle:string = ''


  lib:DiceGuardiansModuleService
  constructor(
    lib:DiceGuardiansModuleService
  ) {


    this.lib = lib

    const self = this

    this.lib.board.events.on('tutorial', async (payload:any) => {

      if (payload['pointerStyle']){
        self.tutorialPointerStyle = payload.pointerStyle
      }

      if (payload['parentStyle']){
        self.tutorialParentStyle = payload.parentStyle
      }


    })
  }

  ngOnInit() {
  }

  toggle(panel:string){
    switch (panel){
      case 'left' :
        if (this.leftToggleName === 'arrow-forward-outline'){
          // was collapsed
          this.leftToggleName = 'arrow-back-outline'
          this.leftDrawerClass = 'shown'
        } else {
          this.leftToggleName = 'arrow-forward-outline'
          this.leftDrawerClass = 'hidden'
        }
      break

      case 'bottom' :
        if (this.bottomToggleName === 'arrow-up-outline'){
          // was collapsed
          this.bottomToggleName = 'arrow-down-outline'
          this.bottomDrawerClass = 'shown'
        } else {
          this.bottomToggleName = 'arrow-up-outline'
          this.bottomDrawerClass = 'hidden'
        }

      break

      default:
      throw new Error('Unrecognized toggle target `' + panel + '`')
    }
  }


}
